// Get icon of field.
// Example usage: {{ field | getFieldIcon }}
export default (field) => {
  let result = "";
  switch (field.field_type) {
    case "TextField":
      if (!field.textarea) {
        result = "note-text-outline";
      } else {
        result = "script-text-outline";
      }
      break;
    case "SlugField":
      result = "snake";
      break;
    case "ColorField":
      result = "palette-outline";
      break;
    case "DateField":
    case "DateTimeField":
      result = "calendar";
      break;
    case "EmailField":
      result = "email-outline";
      break;
    case "PasswordField":
      result = "lock-outline";
      break;
    case "PhoneField":
      result = "phone";
      break;
    case "URLField":
      result = "wan";
      break;
    case "FileField":
      result = "paperclip";
      break;
    case "ImageField":
      result = "camera-outline";
      break;
    case "IPField":
      result = "account-network";
      break;
    default:
      if (field.input_type === "number") {
        result = "numeric";
      } else if (field.input_type === "range") {
        result = "arrow-split-vertical";
      } else if (field.field_type.includes("Choice")) {
        result = "form-select";
      }
  }
  return `mdi-${result}`;
};
