<template>
  <v-app-bar app dense fixed color="btnPrimary">
    <!-- Menu -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-app-bar-nav-icon
          style="color: #fff"
          @click="setOpenPanelServiceList(true)"
          v-bind="attrs"
          v-on="on"
        ></v-app-bar-nav-icon>
      </template>
      <span>{{ $t("message.44") }}</span>
    </v-tooltip>
    <v-toolbar-title class="white--text">{{ brand }}</v-toolbar-title>
    <v-spacer></v-spacer>
    <!-- Light or dark theme button -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" @click="changeTheme()">
          <v-icon color="white">mdi-brightness-4</v-icon>
        </v-btn>
      </template>
      <span>{{ $t("message.1") }}</span>
    </v-tooltip>
    <!-- The choice of color theme -->
    <v-menu rounded="lg" offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          small
          rounded
          outlined
          color="white"
          class="mx-2"
          v-bind="attrs"
          v-on="on"
        >
          {{ $t("message.36") }}
        </v-btn>
      </template>
      <v-list dense>
        <v-list-item-group v-model="updateSelectedTheme" color="primaryTwo">
          <v-list-item
            v-for="(color, idx) in colorList"
            :key="idx"
            @click="
              changeColorTheme({
                color: color.name,
                themes: $vuetify.theme.themes,
              })
            "
          >
            <v-list-item-content
              class="rounded color_sample"
              :style="`background-color: ${color.code};`"
            >
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-menu>
    <!-- Choice localization of application -->
    <v-menu rounded="lg" offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          small
          rounded
          outlined
          color="white"
          class="mx-2"
          v-bind="attrs"
          v-on="on"
        >
          {{ currentLang }}
        </v-btn>
      </template>
      <v-list dense>
        <v-list-item-group v-model="updateSelectedLang" color="primaryTwo">
          <v-list-item
            v-for="(lang, idx) in langList"
            :key="idx"
            @click="changeLocalization(lang)"
          >
            <v-list-item-content>
              <v-list-item-title>{{ lang.name }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-menu>
    <!-- Logout -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" @click="logout()">
          <v-icon color="white">mdi-logout</v-icon>
        </v-btn>
      </template>
      <span>{{ $t("message.2") }}</span>
    </v-tooltip>
  </v-app-bar>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";

export default {
  name: "AppBar",

  computed: {
    ...mapState([
      "brand",
      "openPanelServiceList",
      "colorThemes",
      "authenticityToken",
    ]),
    ...mapState("appBar", [
      "selectedTheme",
      "colorList",
      "selectedLang",
      "langList",
      "currentLang",
    ]),
    updateSelectedTheme: {
      get: function () {
        return this.selectedTheme;
      },
      set: function (num) {
        this.setSelectedTheme(num);
      },
    },
    updateSelectedLang: {
      get: function () {
        return this.selectedLang;
      },
      set: function (langCode) {
        this.setSelectedLang(langCode);
      },
    },
  },

  methods: {
    ...mapMutations([
      "setIsAuthenticated",
      "setAuthenticityToken",
      "setOpenPanelServiceList",
    ]),
    ...mapMutations("popUpMsg", ["setShowMsg"]),
    ...mapMutations("appBar", [
      "setSelectedTheme",
      "setSelectedLang",
      "setCurrentLang",
    ]),
    ...mapActions(["updateStyleVMain"]),
    ...mapActions("appBar", ["changeColorTheme", "changeColorList"]),
    ...mapActions("pageLockout", ["runShowOverlayPageLockout"]),
    ...mapActions("popUpMsg", ["runShowMsg"]),
    //
    changeTheme() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      this.changeColorList(this.$vuetify.theme.dark);
      this.updateStyleVMain(this.$vuetify.theme.dark);
    },
    // Change current language
    changeLocalization(lang) {
      this.setShowMsg(false);
      this.setCurrentLang(lang.name);
      this.runShowOverlayPageLockout(true);
      this.axios
        .post("/admin/change-lang", {
          lang_code: lang.code,
          authenticity_token: this.authenticityToken,
        })
        .then((response) => {
          const data = response.data;
          if (!data.is_authenticated) {
            this.setIsAuthenticated(false);
          } else {
            location.reload();
          }
        })
        .catch((error) => {
          console.log(error);
          this.runShowMsg({ text: error, status: "error" });
        });
    },
    //
    logout() {
      this.setShowMsg(false);
      this.axios
        .post("/admin/logout", { authenticity_token: this.authenticityToken })
        .then(() => {
          this.setAuthenticityToken("");
          this.setIsAuthenticated(false);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

<style lang="css" scoped>
.color_sample {
  width: 100%;
  min-width: 5px;
}
</style>
