<template>
  <v-navigation-drawer
    app
    fixed
    bottom
    temporary
    v-model="updateOpenPanelServiceList"
    :width="updatePanelWidthServiceList"
    color="whiteGrey"
    class="pa-1"
  >
    <!-- Change the width of the panel. -->
    <v-list-item
      class="panel-width-service-list px-0 d-none d-lg-flex d-xl-flex"
    >
      <v-list-item-content class="pb-5">
        <v-slider
          dense
          hide-details
          color="primaryTwo"
          v-model="updatePanelWidthServiceList"
          min="256"
          max="600"
          class="panel-width-service-list__slider ml-5"
        ></v-slider>
      </v-list-item-content>
    </v-list-item>
    <!-- List of services (applications) and collections in them. -->
    <v-expansion-panels class="service-list">
      <!-- Service list -->
      <v-expansion-panel
        v-for="(item, indexService) in serviceList"
        :key="item.service.title"
      >
        <v-expansion-panel-header
          class="text-subtitle-1 font-weight-medium pl-0"
        >
          <div class="pl-1 service-list__icon">
            <v-icon
              color="primary"
              class="pos-relative pos-top-n2"
              v-text="`mdi-${item.service.icon}`"
            ></v-icon>
          </div>
          <span class>{{ item.service.title }}</span>
        </v-expansion-panel-header>
        <v-expansion-panel-content class="service-list__content">
          <!-- Ccollection list -->
          <v-list nav shaped dense>
            <v-list-item-group
              v-model="selectedService[indexService]"
              color="primaryTwo"
            >
              <v-list-item
                v-for="(collection, indexCollection) in item.collections"
                :key="collection.model_key"
                class="px-1"
                @click="
                  [
                    closePopUp(),
                    changeStateCollection(indexService, indexCollection),
                    resetPreActivatedService(indexService),
                    goToDocList(
                      item.service.title,
                      indexService,
                      collection.title,
                      indexCollection
                    ),
                  ]
                "
              >
                <v-list-item-icon class="mr-2">
                  <v-icon>mdi-circle-medium</v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title
                    class="text-subtitle-1 font-weight-regular"
                    v-text="collection.title"
                  ></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </v-navigation-drawer>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
import slug from "slug";

export default {
  name: "NavigationBar",

  data: () => ({
    currentIndexCollection: null,
    isSelectedCollection: false,
  }),

  watch: {
    selectedService() {
      this.setOpenPanelServiceList(false);
    },
  },

  computed: {
    ...mapState([
      "openPanelServiceList",
      "panelWidthServiceList",
      "selectedService",
      "serviceList",
    ]),
    ...mapState("navigationBar", ["currentIndexService"]),
    updateOpenPanelServiceList: {
      get: function () {
        return this.openPanelServiceList;
      },
      set: function (flag) {
        this.setOpenPanelServiceList(flag);
      },
    },
    updatePanelWidthServiceList: {
      get: function () {
        return this.panelWidthServiceList;
      },
      set: function (num) {
        this.setPanelWidthServiceList(num);
      },
    },
  },

  methods: {
    ...mapMutations([
      "setOpenPanelServiceList",
      "setPanelWidthServiceList",
      "setSelectedService",
    ]),
    ...mapMutations("navigationBar", ["setCurrentIndexService"]),
    ...mapMutations("popUpMsg", ["setShowMsg"]),
    ...mapActions("pageLockout", ["runShowOverlayPageLockout"]),
    // Close all pop-up windows.
    closePopUp: function () {
      this.setShowMsg(false);
      this.runShowOverlayPageLockout(false);
    },
    // Change state of collection - Active or not active.
    changeStateCollection: function (indexService, indexCollection) {
      if (
        this.currentIndexService === null ||
        indexService !== this.currentIndexService ||
        indexCollection !== this.currentIndexCollection
      ) {
        this.setCurrentIndexService(indexService);
        this.currentIndexCollection = indexCollection;
        this.isSelectedCollection = true;
      } else {
        this.setCurrentIndexService(null);
        this.currentIndexCollection = null;
        this.isSelectedCollection = false;
      }
    },
    // List of services - Resetting previously activated items.
    resetPreActivatedService: function (currIndex) {
      this.setSelectedService(
        this.selectedService.map(function (item, idx) {
          if (idx !== currIndex) {
            item = undefined;
          }
          return item;
        })
      );
    },
    // Create Url for Document list.
    goToDocList: function (
      serviceTitle,
      indexService,
      collectionTitle,
      indexCollection
    ) {
      if (this.isSelectedCollection) {
        // Go to Blank page
        this.$router.replace({ name: "blank" });
        //
        const currentUserLocale = this.$i18n.locale;
        const slugServiceTitle = slug(serviceTitle, {
          locale: currentUserLocale,
        });
        const slugCollectionTitle = slug(collectionTitle, {
          locale: currentUserLocale,
        });
        setTimeout(() => {
          this.$router.replace({
            name: "docList",
            params: {
              service: slugServiceTitle,
              indexService: indexService,
              collection: slugCollectionTitle,
              indexCollection: indexCollection,
            },
          });
        }, 500);
      } else {
        // Go to Home page
        this.$router.replace({ name: "home" });
      }
    },
  },
};
</script>

<style>
.panel-width-service-list__slider .v-slider {
  max-width: 195px;
}
.service-list__icon {
  max-width: 37px;
}
</style>
