export default {
  namespaced: true,

  state: {
    selectedTheme: 0,
    colorList: [
      { name: "grey", code: "#9E9E9E" },
      { name: "blue", code: "#2196F3" },
      { name: "teal", code: "#009688" },
      { name: "pink", code: "#E91E63" },
      { name: "green", code: "#4CAF50" },
      { name: "purple", code: "#9C27B0" },
      { name: "orange", code: "#E65100" },
      { name: "brown", code: "#795548" },
    ],
    selectedLang: 0,
    langList: [],
    currentLang: "English",
  },

  getters: {},

  mutations: {
    setSelectedTheme(state, num) {
      state.selectedTheme = num;
    },
    setColorList(state, arr) {
      state.colorList = arr;
    },
    setSelectedLang(state, langCode) {
      state.selectedLang = langCode;
    },
    setLangList(state, arr) {
      state.langList = arr;
    },
    setCurrentLang(state, langName) {
      state.currentLang = langName;
    },
  },

  actions: {
    // The choice of color theme
    changeColorTheme({ rootState }, payload) {
      const color = payload.color;
      const themes = payload.themes;
      let theme = rootState.colorThemes.light[color];
      themes.light.primary = theme.primary;
      themes.light.primaryTwo = theme.primaryTwo;
      themes.light.btnPrimary = theme.btnPrimary;
      theme = rootState.colorThemes.dark[color];
      themes.dark.primary = theme.primary;
      themes.dark.primaryTwo = theme.primaryTwo;
      themes.dark.btnPrimary = theme.btnPrimary;
    },
    // Update color list
    changeColorList({ state, commit, rootState }, dark_theme) {
      let theme = dark_theme
        ? rootState.colorThemes.dark
        : rootState.colorThemes.light;
      let colorList = state.colorList.map(function (color) {
        color.code = theme[color.name].btnPrimary;
        return color;
      });
      commit("setColorList", colorList);
    },
  },
};
