<template>
  <!-- Pop-up messages -->
  <v-snackbar
    app
    top
    vertical
    multi-line
    :outlined="!$vuetify.theme.dark"
    :color="$vuetify.theme.dark ? '#181a1f' : status_txt[msgStatus]"
    timeout="-1"
    v-model="compShowMsg"
  >
    <span v-html="textMsg" :class="`${status_txt[msgStatus]}--text`"></span>
    <template v-slot:action="{ attrs }">
      <v-btn
        text
        small
        v-bind="attrs"
        :class="`${status_txt[msgStatus]}--text`"
        @click="compShowMsg = false"
      >
        {{ $t("message.18") }}
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "PopUpMsg",

  data: () => ({
    status_txt: {
      info: "txtInfo",
      error: "txtError",
      success: "txtSuccess",
      warning: "txtWarning",
    },
  }),

  computed: {
    ...mapState("popUpMsg", ["showMsg", "textMsg", "msgStatus"]),
    compShowMsg: {
      get: function () {
        return this.showMsg;
      },
      set: function (flag) {
        this.setShowMsg(flag);
      },
    },
  },

  methods: {
    ...mapMutations("popUpMsg", ["setShowMsg"]),
  },
};
</script>
