export default {
  namespaced: true,

  state: {
    currentIndexService: null,
  },

  getters: {},

  mutations: {
    setCurrentIndexService(state, payload) {
      state.currentIndexService = payload;
    },
  },

  actions: {
    // Reset states of the Menu - Cancel the selected items.
    resetStateMenu({ commit, rootState }) {
      commit("setCurrentIndexService", null);
      const arr = rootState.selectedService.map(function () {
        return undefined;
      });
      commit("setSelectedService", arr, { root: true });
    },
  },
};
