<template>
  <v-app>
    <template v-if="isAuthenticated">
      <AppBar />
      <NavigationBar />
      <PopUpMsg />
      <PageLockout />
      <v-main :style="styleVMain">
        <router-view />
      </v-main>
    </template>
    <template v-else>
      <router-view v-if="$route.name === 'signin'" />
    </template>
  </v-app>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
import NavigationBar from "@/components/NavigationBar.vue";
import AppBar from "@/components/AppBar.vue";
import PopUpMsg from "@/components/PopUpMsg.vue";
import PageLockout from "@/components/PageLockout.vue";

export default {
  name: "App",

  components: {
    NavigationBar,
    AppBar,
    PopUpMsg,
    PageLockout,
  },

  computed: {
    ...mapState([
      "brand",
      "slogan",
      "styleVMain",
      "isAuthenticated",
      "authenticityToken",
      "serviceList",
    ]),
    ...mapState("appBar", ["colorList", "langList"]),
  },

  watch: {
    isAuthenticated: function (flag) {
      if (flag) {
        // Get a list of services and collections
        if (this.serviceList.length === 0) {
          this.getServiceList();
        }
        // Go to Home page
        this.$router.replace({ name: "home" });
        //
        setTimeout(() => {
          this.setOpenPanelServiceList(true);
        }, 2000);
      } else {
        if (this.authenticityToken.length === 0) {
          location.reload();
        }
        this.setShowMsg(false);
        this.runShowOverlayPageLockout(false);
        this.setOpenPanelServiceList(false);
        this.resetStateMenu();
        this.setDocsPerPage(50);
        this.resetToDefault();
        // Go to Login page
        this.$router.replace({ name: "signin" });
      }
    },
  },

  methods: {
    ...mapMutations([
      "setLogo",
      "setBrand",
      "setSlogan",
      "setBgImage",
      "setBgOpacity",
      "setIsAuthenticated",
      "setAuthenticityToken",
      "setSelectedService",
      "setServiceList",
      "setOpenPanelServiceList",
    ]),
    ...mapMutations("ckeditor", ["setLanguageCKEditor"]),
    ...mapMutations("appBar", [
      "setSelectedTheme",
      "setSelectedLang",
      "setLangList",
      "setCurrentLang",
    ]),
    ...mapMutations("popUpMsg", ["setShowMsg"]),
    ...mapActions(["updateStyleVMain"]),
    ...mapActions("navigationBar", ["resetStateMenu"]),
    ...mapActions("popUpMsg", ["runShowMsg"]),
    ...mapActions("docList", ["setDocsPerPage", "resetToDefault"]),
    ...mapActions("appBar", ["changeColorTheme", "changeColorList"]),
    ...mapActions("pageLockout", ["runShowOverlayPageLockout"]),
    // Get a list of services and collections
    getServiceList() {
      if (this.isAuthenticated) {
        this.axios
          .post("/admin/service-list", {
            authenticity_token: this.authenticityToken,
          })
          .then((response) => {
            const data = response.data;
            if (!data.is_authenticated) {
              this.setIsAuthenticated(false);
              return;
            }
            const listLength = data.service_list.length;
            if (listLength > 0) {
              const selectedServiceList = [];
              for (let idx = 0; idx < listLength; idx++) {
                selectedServiceList.push(undefined);
              }
              this.setSelectedService(selectedServiceList);
              this.setServiceList(data.service_list);
            } else {
              console.log("No data available");
              this.runShowMsg({
                text: "No data available",
                status: "warning",
              });
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
  },

  created() {
    // Get language code and parameters for admin panal.
    this.axios
      .post("/admin/get-parameters")
      .then((response) => {
        const data = response.data;
        // Set logo
        this.setLogo(data.logo);
        // Set brand and slogan
        this.setBrand(data.brand);
        this.setSlogan(data.slogan);
        // Set bckground
        this.setBgImage(data.bg_image);
        this.setBgOpacity(data.bg_opacity);
        // Set locale
        if (data.lang_code.length > 0) {
          this.$i18n.locale = data.lang_code;
        } else {
          this.$i18n.locale = this.$userLang;
        }
        this.setLanguageCKEditor(this.$i18n.locale);
        this.setLangList([
          {
            name: "English",
            code: "en",
          },
          { name: "Русский", code: "ru" },
          { name: "Esperanto", code: "eo" },
        ]);
        const currLocale = this.$i18n.locale;
        for (let idx = 0; idx < this.langList.length; idx++) {
          const lang = this.langList[idx];
          if (lang.code === currLocale) {
            this.setSelectedLang(idx);
            this.setCurrentLang(lang.name);
            break;
          }
        }
        // Set color theme.
        this.$vuetify.theme.dark = data.dark_theme;
        this.changeColorList(this.$vuetify.theme.dark);
        this.updateStyleVMain(this.$vuetify.theme.dark);
        const currColorTheme = data.color_theme;
        for (let idx = 0; idx < this.colorList.length; idx++) {
          const color = this.colorList[idx];
          if (color.name === currColorTheme) {
            this.setSelectedTheme(idx);
            this.changeColorTheme({
              color: color.name,
              themes: this.$vuetify.theme.themes,
            });
            break;
          }
        }
        //
        this.setAuthenticityToken(data.csrf);
        this.setIsAuthenticated(data.is_authenticated);
      })
      .catch((error) => {
        window.console.log(error);
        this.runShowMsg({
          text: String(error),
          status: "error",
        });
      });
  },
};
</script>

