export default {
  // Update style for v-main.
  updateStyleVMain({ commit, rootState }, dark_theme) {
    if (rootState.bgImage.length > 0) {
      let color = dark_theme ? "19, 21, 25" : "255, 255, 255";
      let style =
        `background-image: url('${rootState.bgImage}');` +
        "background-repeat: no-repeat;" +
        "background-position: center;" +
        "background-attachment: fixed;" +
        "background-size: cover;" +
        `background-color: rgba(${color}, ${rootState.bgOpacity});` +
        "background-blend-mode: overlay;";
      commit("setStyleVMain", style, { root: true });
    }
  },
};
