import Vue from "vue";

export default {
  namespaced: true,

  state: {
    documents: [],
    currentPageNumber: 1,
    pageCount: 0,
    progressionStep: 1,
    docsPerPage: 50,
    sortDocList: "alphabetical_links",
    sortDirectDocList: -1,
    sortTypes: ["alphabetical_links", "created", "updated"],
    searchQuery: null,
    deleteAllDocsFlag: false,
    docsToBeDelete: [],
    docUrlWithoutID: "",
    // Data of filter by categories.
    dataFilterTwo: [],
    selectedDataFilterTwo: {},
  },

  getters: {},

  mutations: {
    setDocuments(state, arr) {
      state.documents = arr;
    },
    setCurrentPageNumber(state, num) {
      state.currentPageNumber = num;
    },
    setPageCount(state, num) {
      state.pageCount = num;
    },
    setProgressionStep(state, num) {
      state.progressionStep = num;
    },
    setDocsPerPage(state, num) {
      state.docsPerPage = num;
    },
    setSortDocList(state, name) {
      state.sortDocList = name;
    },
    setSortDirectDocList(state, num) {
      state.sortDirectDocList = num;
    },
    setSearchQuery(state, text) {
      state.searchQuery = text;
    },
    setDeleteAllDocsFlag(state, flag) {
      state.deleteAllDocsFlag = flag;
    },
    setDocsToBeDelete(state, arr) {
      state.docsToBeDelete = arr;
    },
    setDocUrlWithoutID(state, text) {
      state.docUrlWithoutID = text;
    },
    setDataFilterTwo(state, arr) {
      state.dataFilterTwo = arr;
    },
    setSelectedDataFilterTwo(state, obj) {
      state.selectedDataFilterTwo = obj;
    },
  },

  actions: {
    // Get a list of documents.
    ajaxGetDocList({ state, commit, rootState }, payload) {
      return new Promise((resolve, reject) => {
        // Filter for selective fields.
        const filterTwo = {};
        for (let idx = 0; idx < state.dataFilterTwo.length; idx++) {
          const df = state.dataFilterTwo[idx];
          const key = df.field;
          const value = state.selectedDataFilterTwo[key];
          if (value !== null) {
            if (Array.isArray(value)) {
              if (value.length > 0) {
                filterTwo[key] = {
                  value: JSON.stringify(value.map(String)),
                  negation: df.negation,
                };
              }
            } else {
              filterTwo[key] = {
                value: String(value),
                negation: df.negation,
              };
            }
          }
        }
        const payloadQuery = {
          model_key: payload.modelKey,
          fields_name: payload.fieldsName,
          page_num: state.currentPageNumber,
          search_query: state.searchQuery || "",
          limit: state.docsPerPage,
          sort: state.sortDocList,
          direct: state.sortDirectDocList,
          filter_2: JSON.stringify(filterTwo),
          authenticity_token: rootState.authenticityToken,
        };
        Vue.axios
          .post("/admin/document-list", payloadQuery)
          .then((response) => {
            const data = response.data;
            if (!data.is_authenticated) {
              commit("setIsAuthenticated", false, { root: true });
            } else {
              commit("setPageCount", data.page_count);
              commit("setDocuments", data.documents);
              resolve();
            }
          })
          .catch((error) => {
            window.console.log(error);
            reject(error);
          });
      });
    },
    // Get data of filter by categories.
    ajaxGetFilters({ commit, rootState }, model_key) {
      return new Promise((resolve, reject) => {
        Vue.axios
          .post("/admin/get-filters", {
            model_key,
            authenticity_token: rootState.authenticityToken,
          })
          .then((response) => {
            const data = response.data;
            if (!data.is_authenticated) {
              commit("setIsAuthenticated", false, { root: true });
            } else {
              const filterTwo = data.filter_2;
              const selectedDataFilterTwo = {};
              filterTwo.sort(function (item, item2) {
                if (item.label > item2.label) {
                  return 1;
                }
                if (item.label < item2.label) {
                  return -1;
                }
                return 0;
              });
              for (const item of filterTwo) {
                selectedDataFilterTwo[item.field] = null;
              }
              commit("setDataFilterTwo", filterTwo);
              commit("setSelectedDataFilterTwo", selectedDataFilterTwo);
              resolve();
            }
          })
          .catch((error) => {
            window.console.log(error);
            reject(error);
          });
      });
    },
    // Reset the values of all default parameters.
    resetToDefault({ commit }) {
      commit("setDocuments", []);
      commit("setCurrentPageNumber", 1);
      commit("setProgressionStep", 1);
      commit("setPageCount", 0);
      commit("setSortDocList", "alphabetical_links");
      commit("setSortDirectDocList", -1);
      commit("setSearchQuery", null);
      commit("setDeleteAllDocsFlag", false);
      commit("setDocsToBeDelete", []);
      commit("setDocUrlWithoutID", "");
      commit("setDataFilterTwo", []);
      commit("setSelectedDataFilterTwo", {});
    },
  },
};
