import Vue from "vue";
import App from "@/App.vue";
import VueI18n from "vue-i18n";
import router from "@/router";
import store from "@/store";
import messages from "@/i18n/messages";
import vuetify from "@/plugins/vuetify";
import truncate from "@/filters/truncate";
import getFileIcon from "@/filters/get_file_icon";
import getFieldIcon from "@/filters/get_field_icon";
import "@/assets/css/helpers.css";
import axios from "axios";
import VueAxios from "vue-axios";
import CKEditor from "@ckeditor/ckeditor5-vue2";
import "@/plugins/ckeditor_translations.js";

// Disable debug to production.
if (process.env.NODE_ENV === "production") {
  Vue.config.productionTip = false;
  Vue.config.devtools = false;
  Vue.config.debug = false;
  Vue.config.silent = true;
}

Vue.use(VueI18n);
Vue.use(VueAxios, axios);
Vue.use(CKEditor);

Vue.axios.defaults.headers.common["Content-Type"] = "application/json";

Vue.filter("truncate", truncate);
Vue.prototype.$getFileIcon = getFileIcon;
Vue.prototype.$getFieldIcon = getFieldIcon;

// Determine language code by browser localization.
Vue.prototype.$userLang = (navigator.language || navigator.userLanguage)
  .slice(0, 2)
  .toLowerCase();

// Create VueI18n instance with options.
const i18n = new VueI18n({
  locale: "en",
  fallbackLocale: "en",
  messages,
});

new Vue({
  router,
  store,
  i18n,
  vuetify,
  render: (h) => h(App),
}).$mount("#app");
